@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville);
.page-container {
  width: 100vw;
  height: 100vh;
  padding-top: 15px; }
  .page-container.blue {
    background: #fafafa -webkit-gradient(linear, left top, left bottom, from(#ccddff), to(#fafafa)) no-repeat;
    background: #fafafa -webkit-linear-gradient(#ccddff, #fafafa) no-repeat;
    background: #fafafa linear-gradient(#ccddff, #fafafa) no-repeat; }
    .page-container.blue ::-moz-selection {
      background-color: #04c;
      color: #fafafa; }
    .page-container.blue ::selection {
      background-color: #04c;
      color: #fafafa; }
    .page-container.blue a:hover {
      color: #04c; }
  .page-container.pink {
    background: #fafafa -webkit-gradient(linear, left top, left bottom, from(#ff9bff), to(#fafafa)) no-repeat;
    background: #fafafa -webkit-linear-gradient(#ff9bff, #fafafa) no-repeat;
    background: #fafafa linear-gradient(#ff9bff, #fafafa) no-repeat; }
    .page-container.pink ::-moz-selection {
      background-color: #9b009b;
      color: #fafafa; }
    .page-container.pink ::selection {
      background-color: #9b009b;
      color: #fafafa; }
    .page-container.pink a:hover {
      color: #9b009b; }
  .page-container.yellow {
    background: #fafafa -webkit-gradient(linear, left top, left bottom, from(#ebff9b), to(#fafafa)) no-repeat;
    background: #fafafa -webkit-linear-gradient(#ebff9b, #fafafa) no-repeat;
    background: #fafafa linear-gradient(#ebff9b, #fafafa) no-repeat; }
    .page-container.yellow ::-moz-selection {
      background-color: #7c9b00;
      color: #fafafa; }
    .page-container.yellow ::selection {
      background-color: #7c9b00;
      color: #fafafa; }
    .page-container.yellow a:hover {
      color: #7c9b00; }
  .page-container.green {
    background: #fafafa -webkit-gradient(linear, left top, left bottom, from(#98ff8e), to(#fafafa)) no-repeat;
    background: #fafafa -webkit-linear-gradient(#98ff8e, #fafafa) no-repeat;
    background: #fafafa linear-gradient(#98ff8e, #fafafa) no-repeat; }
    .page-container.green ::-moz-selection {
      background-color: #0d8e00;
      color: #fafafa; }
    .page-container.green ::selection {
      background-color: #0d8e00;
      color: #fafafa; }
    .page-container.green a:hover {
      color: #0d8e00; }

h1,
p {
  font-family: "Libre Baskerville", serif;
  display: inline-block;
  position: relative;
  margin: 0 0 20px;
  padding: 15px 20px;
  background: #fafafa;
  box-shadow: rgba(0, 0, 0, 0.9) 0 1px 4px, rgba(0, 0, 0, 0.05) 0 0 60px inset; }

a {
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

